.two {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1.5fr;
  gap: 20px px;
  grid-template-areas: ". . two__main two__main two__main two__main two__main two__main two__main two__main two__main. .";
  justify-content: center;
  align-content: center;
  justify-items: stretch;
  align-items: center;
  font-family: "Satoshi";
}

.two__main {
  grid-area: two__main;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.two__mainImg > img {
  width: 7.36rem;
}

.two__mainText > p {
  font-weight: 400;
  font-size: 1.82rem;
  font-family: "Inter";
}

.two__mainText {
  margin-top: -20px;
}
